import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { syncTodosWithFirebase } from '../../store/todoSlice';
import { syncHashtagsWithFirebase } from '../../store/hashtagSlice';
const useFirebaseSync = () => {
    const dispatch = useDispatch();
    const todosLoaded = useSelector((state) => state.todos.fetchComplete);
    const hashtagsLoaded = useSelector((state) => state.hashtags.fetchComplete);
    const todos = useSelector((state) => state.todos.todos);
    const hashtags = useSelector((state) => state.hashtags.tags);
    useEffect(() => {
        if (todosLoaded) {
            dispatch(syncTodosWithFirebase(todos));
        }
    }, [todosLoaded, todos]);
    useEffect(() => {
        if (hashtagsLoaded) {
            dispatch(syncHashtagsWithFirebase(hashtags));
        }
    }, [hashtagsLoaded, hashtags]);
};
export default useFirebaseSync;
