import { useSelector } from 'react-redux';
export const groupTitleFormat = (key) => {
    if (key === 'Выполненные задачи') {
        return 'Выполненные задачи';
    }
    const groupCase = useSelector((state) => state.options.groupCase);
    const formatDate = (key) => {
        switch (key) {
            case 'Без даты':
            case 'Просрочено':
            case 'Сегодня':
            case 'Завтра':
            case 'Следующие 7 дней':
            case 'Следующие 30 дней':
            case 'Позже':
                return key;
            default:
                return 'Неизвестная группа';
        }
    };
    const groupByKeyTranslations = {
        none: 'Нет',
        low: 'Низкий приоритет',
        medium: 'Средний приоритет',
        high: 'Высокий приоритет',
    };
    switch (groupCase) {
        case 'targetDate':
            return formatDate(key);
        case 'priority':
            return groupByKeyTranslations[key] || 'Нет приоритета';
        case 'tags':
            return key === 'none' ? 'Нет меток' : key;
        case 'none':
        default:
            return '';
    }
};
