import { createSlice } from '@reduxjs/toolkit';
const initialState = { groupCase: 'none', sortCase: 'none', showTagsCase: false };
const optionsSlice = createSlice({
    name: 'options',
    initialState,
    reducers: {
        switchGroupCase: (state, action) => {
            state.groupCase = action.payload;
        },
        switchSortCase: (state, action) => {
            state.sortCase = action.payload;
        },
        switchShow: (state) => {
            state.showTagsCase = !state.showTagsCase;
        },
    },
});
export const { switchGroupCase, switchSortCase, switchShow } = optionsSlice.actions;
export default optionsSlice.reducer;
