import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { fetchTodosFromFirebase } from '../../store/todoSlice';
import { fetchHashtagsFromFirebase } from '../../store/hashtagSlice';
const useFetchFirebase = () => {
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(fetchTodosFromFirebase());
        dispatch(fetchHashtagsFromFirebase());
    }, []);
};
export default useFetchFirebase;
