import { useMemo } from 'react';
import { useSelector } from 'react-redux';
const useTodosSortAndGroup = () => {
    const todos = useSelector((state) => state.todos.todos);
    const sortCase = useSelector((state) => state.options.sortCase);
    const groupCase = useSelector((state) => state.options.groupCase);
    const todosSortAndGroup = useMemo(() => {
        const todosArray = Object.values(todos);
        const sortedTodosArray = todosArray.sort((todoA, todoB) => {
            switch (sortCase) {
                case 'targetDate': {
                    const dateA = todoA.targetDate ? new Date(todoA.targetDate).getTime() : null;
                    const dateB = todoB.targetDate ? new Date(todoB.targetDate).getTime() : null;
                    if (dateA === null)
                        return 1;
                    if (dateB === null)
                        return -1;
                    return dateA - dateB;
                }
                case 'content': {
                    return todoA.content.localeCompare(todoB.content);
                }
                case 'tags': {
                    const tagA = todoA.tags[0] || '';
                    const tagB = todoB.tags[0] || '';
                    return tagA.localeCompare(tagB);
                }
                case 'priority': {
                    const priorityOrder = { high: 1, medium: 2, low: 3, none: 4 };
                    return priorityOrder[todoA.priority] - priorityOrder[todoB.priority];
                }
                case 'createDate': {
                    const dateA = todoA.timeOfCreation ? new Date(todoA.timeOfCreation).getTime() : null;
                    const dateB = todoB.timeOfCreation ? new Date(todoB.timeOfCreation).getTime() : null;
                    if (dateA === null)
                        return 1;
                    if (dateB === null)
                        return -1;
                    return dateA - dateB;
                }
                case 'none':
                default:
                    return 0;
            }
        });
        const doneTodosArray = sortedTodosArray.filter((todo) => todo.doneStatus).map((todo) => todo.id);
        if (groupCase === 'none') {
            return {
                groupsOfTodos: { '': sortedTodosArray.filter((todo) => !todo.doneStatus).map((todo) => todo.id) },
                doneTodos: doneTodosArray,
            };
        }
        const groupedTodosArrays = sortedTodosArray
            .filter((todo) => !todo.doneStatus)
            .reduce((acc, todo) => {
            const groupValues = [];
            if (groupCase === 'targetDate') {
                const now = new Date();
                const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
                if (!todo.targetDate) {
                    groupValues.push('Без даты');
                }
                else {
                    const targetDate = new Date(todo.targetDate);
                    const targetDay = new Date(targetDate.getFullYear(), targetDate.getMonth(), targetDate.getDate());
                    if (targetDay < today) {
                        groupValues.push('Просрочено');
                    }
                    else if (targetDay.getTime() === today.getTime()) {
                        groupValues.push('Сегодня');
                    }
                    else {
                        const tomorrow = new Date(today);
                        tomorrow.setDate(today.getDate() + 1);
                        const next7Days = new Date(today);
                        next7Days.setDate(today.getDate() + 7);
                        const next30Days = new Date(today);
                        next30Days.setDate(today.getDate() + 30);
                        if (targetDay.getTime() === tomorrow.getTime()) {
                            groupValues.push('Завтра');
                        }
                        else if (targetDay <= next7Days) {
                            groupValues.push('Следующие 7 дней');
                        }
                        else if (targetDay <= next30Days) {
                            groupValues.push('Следующие 30 дней');
                        }
                        else {
                            groupValues.push('Позже');
                        }
                    }
                }
            }
            else if (groupCase === 'tags') {
                const values = Array.isArray(todo[groupCase]) ? todo[groupCase] : [todo[groupCase]];
                values.forEach((value) => {
                    groupValues.push(value);
                });
            }
            else if (groupCase === 'priority') {
                const value = todo[groupCase];
                groupValues.push(value);
            }
            groupValues.forEach((groupValue) => {
                if (!acc[groupValue]) {
                    acc[groupValue] = [];
                }
                acc[groupValue].push(todo.id);
            });
            return acc;
        }, {});
        return { groupsOfTodos: groupedTodosArrays, doneTodos: doneTodosArray };
    }, [todos, groupCase, sortCase]);
    return todosSortAndGroup;
};
export default useTodosSortAndGroup;
