import { useMemo } from 'react';
import { useSelector } from 'react-redux';
const useSubTodos = (id) => {
    const subTodos = useSelector((state) => state.todos.todos[id].children);
    const subTodosIds = useMemo(() => {
        return Object.keys(subTodos);
    }, [subTodos]);
    return subTodosIds;
};
export default useSubTodos;
